import React, { useState } from "react";
import {
  Box,
  HStack,
  Text,
  MenuRoot,
  MenuTrigger,
  MenuContent,
  MenuItem,
  Stack,
} from "@chakra-ui/react";
import { FiChevronDown, FiPlus } from "react-icons/fi";
import { useBoardStore } from "@/state/BoardStore";
import { AppButton } from "@/ui";

type Props = {
  onCreateBoard: () => void;
};

const BoardSelector: React.FC<Props> = ({ onCreateBoard }) => {
  const { boards, activeBoardId, setActiveBoard, getActiveBoard } =
    useBoardStore();
  const activeBoard = getActiveBoard();

  if (boards.length === 0) {
    return (
      <AppButton
        size="sm"
        variantStyle="primary"
        onClick={onCreateBoard}
        icon={<FiPlus size={14} />}
      >
        Create Board
      </AppButton>
    );
  }

  return (
    <HStack gap={2}>
      <MenuRoot positioning={{ placement: "bottom-start" }}>
        <MenuTrigger asChild>
          <Box
            as="button"
            px={3}
            py={2}
            bg="white"
            border="1px solid"
            borderColor="gray.200"
            borderRadius="md"
            _hover={{ bg: "gray.50", borderColor: "gray.300" }}
            transition="all 0.2s"
            cursor="pointer"
          >
            <HStack gap={2}>
              <Text fontSize="lg">{activeBoard?.icon || ""}</Text>
              <Text fontSize="sm" fontWeight="600" color="gray.900">
                {activeBoard?.name || "Select Board"}
              </Text>
              <Box as={FiChevronDown} fontSize="14px" color="gray.500" />
            </HStack>
          </Box>
        </MenuTrigger>
        <MenuContent
          minW="240px"
          bg="white"
          borderRadius="md"
          border="1px solid"
          borderColor="gray.200"
          boxShadow="lg"
          p={1}
        >
          {boards.map((board) => (
            <MenuItem
              key={board.id}
              value={board.id}
              onClick={() => setActiveBoard(board.id)}
              bg={board.id === activeBoardId ? "blue.50" : "transparent"}
              borderRadius="md"
              px={3}
              py={2}
              cursor="pointer"
              _hover={{ bg: board.id === activeBoardId ? "blue.100" : "gray.50" }}
            >
              <HStack gap={2}>
                <Text fontSize="lg">{board.icon || ""}</Text>
                <Stack gap={0}>
                  <Text fontSize="sm" fontWeight="600" color="gray.900">
                    {board.name}
                  </Text>
                  {board.description && (
                    <Text fontSize="xs" color="gray.500" lineClamp={1}>
                      {board.description}
                    </Text>
                  )}
                </Stack>
              </HStack>
            </MenuItem>
          ))}
          <Box borderTop="1px solid" borderColor="gray.200" mt={1} pt={1}>
            <MenuItem
              value="create"
              onClick={onCreateBoard}
              borderRadius="md"
              px={3}
              py={2}
              cursor="pointer"
              _hover={{ bg: "blue.50" }}
            >
              <HStack gap={2} color="blue.600">
                <FiPlus size={16} />
                <Text fontSize="sm" fontWeight="600">
                  Create New Board
                </Text>
              </HStack>
            </MenuItem>
          </Box>
        </MenuContent>
      </MenuRoot>
    </HStack>
  );
};

export default BoardSelector;
