import React from "react";
import { Box, HStack, Stack, Text } from "@chakra-ui/react";
import {
  FiClock,
  FiCheckCircle,
  FiMessageSquare,
  FiMove,
  FiPlus,
} from "react-icons/fi";
import type { Activity } from "@/types";
import { useAssigneesStore } from "@/state/AssigneesStore";

type Props = {
  activities: Activity[];
};

const ActivityTimeline: React.FC<Props> = ({ activities }) => {
  const { getAssignee } = useAssigneesStore();

  const getActivityIcon = (type: Activity["type"]) => {
    switch (type) {
      case "created":
        return FiPlus;
      case "updated":
        return FiClock;
      case "moved":
        return FiMove;
      case "commented":
        return FiMessageSquare;
      case "completed_subtask":
        return FiCheckCircle;
      default:
        return FiClock;
    }
  };

  const getActivityColor = (type: Activity["type"]) => {
    switch (type) {
      case "created":
        return "green";
      case "updated":
        return "blue";
      case "moved":
        return "purple";
      case "commented":
        return "orange";
      case "completed_subtask":
        return "teal";
      default:
        return "gray";
    }
  };

  const getActivityText = (activity: Activity) => {
    const author = activity.authorId ? getAssignee(activity.authorId) : null;
    const authorName = author?.name || "Someone";

    switch (activity.type) {
      case "created":
        return `${authorName} created this task`;
      case "updated":
        return `${authorName} updated the task`;
      case "moved":
        return `${authorName} moved to ${activity.metadata?.status || "unknown"}`;
      case "commented":
        return `${authorName} added a comment`;
      case "completed_subtask":
        return `${authorName} completed a subtask`;
      default:
        return `${authorName} performed an action`;
    }
  };

  const formatTimestamp = (timestamp: string) => {
    const date = new Date(timestamp);
    const now = new Date();
    const diffMs = now.getTime() - date.getTime();
    const diffMins = Math.floor(diffMs / 60000);
    const diffHours = Math.floor(diffMs / 3600000);
    const diffDays = Math.floor(diffMs / 86400000);

    if (diffMins < 1) return "Just now";
    if (diffMins < 60) return `${diffMins}m ago`;
    if (diffHours < 24) return `${diffHours}h ago`;
    if (diffDays < 7) return `${diffDays}d ago`;
    return date.toLocaleDateString();
  };

  if (activities.length === 0) {
    return (
      <Box
        p={4}
        textAlign="center"
        borderRadius="md"
        bg="gray.50"
        border="1px dashed"
        borderColor="gray.300"
      >
        <Text fontSize="sm" color="gray.500">
          No activity yet
        </Text>
      </Box>
    );
  }

  return (
    <Stack gap={3} position="relative">
      {activities.map((activity, index) => {
        const Icon = getActivityIcon(activity.type);
        const color = getActivityColor(activity.type);
        const isLast = index === activities.length - 1;

        return (
          <HStack key={activity.id} align="flex-start" gap={3}>
            <Box position="relative">
              <Box
                w="32px"
                h="32px"
                borderRadius="full"
                bg={`${color}.100`}
                border="2px solid"
                borderColor={`${color}.400`}
                display="flex"
                alignItems="center"
                justifyContent="center"
                color={`${color}.600`}
                zIndex={1}
              >
                <Icon size={14} />
              </Box>
              {!isLast && (
                <Box
                  position="absolute"
                  left="15px"
                  top="32px"
                  w="2px"
                  h="calc(100% + 12px)"
                  bg="gray.200"
                />
              )}
            </Box>
            <Box flex="1" pt={0.5}>
              <Text fontSize="sm" color="gray.700">
                {getActivityText(activity)}
              </Text>
              <Text fontSize="xs" color="gray.500" mt={0.5}>
                {formatTimestamp(activity.timestamp)}
              </Text>
            </Box>
          </HStack>
        );
      })}
    </Stack>
  );
};

export default ActivityTimeline;
